"use client";

import React from "react";
import { useRouter } from "next/navigation";
import ResourcesList from "../../components/ResourcesList";

const ModulePage = () => {
  const router = useRouter();

  const resources = [
    { id: 1, name: "Recurso 1" },
    { id: 2, name: "Recurso 2" },
  ];

  return (
    <div className="p-6">
      <button
        className="text-primary underline mb-4 flex items-center space-x-2"
        onClick={() => router.push("/")}
      >
        <span>&larr;</span>
        <span>Volver al Inicio</span>
      </button>
      <h1 className="font-bold mb-2 h32">Módulo 1</h1>
      <p className="p16 mb-4">Subtítulo del módulo</p>
      <iframe
        src="https://www.youtube.com/embed/dQw4w9WgXcQ"
        title="Video"
        className="w-full h-64 rounded-lg shadow-md"
        allowFullScreen
      />
      <p className="text-neutral-700 mt-4">
        Este es un módulo sobre un tema importante del nivel.
      </p>
      <ResourcesList resources={resources} />
    </div>
  );
};

export default ModulePage;
